import * as React from "react";
import { GatsbyImage, StaticImage } from "gatsby-plugin-image";
import {thumbContainer, topContainer, banner, backdrop} from "../styles/contentThumbnail.module.css"

const ContentThumbnail = ({image, title, alt, keyname}) => {
    const img = "../images/"+keyname+".png";
    alert(img);

    return(
        <div className={thumbContainer}>
            <div className={topContainer}>
                <div className={banner}>{title}</div>
                <StaticImage
                    src = {img}
                    alt={alt}
                    placeholder="blurred"
                    layout="constrained"
                />
            </div>
            <div className={backdrop}></div>
        </div>
    )
}

export default ContentThumbnail;